body {
  margin: 0;
  background-color: #F4F6F9;
}

.slds-flex_column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slds-img-width_15rem {
  width: 15rem
}

.slds-heigh_100vh {
  height: 100vh;
}

.slds-login-card-body {
  width: 20rem;
  height: 12rem;
}

.slds-notification {
  animation: notificationup 0.2s ease-in-out
}

.slds_cursor-pointer:hover {
  cursor: pointer;
}

.slds-pager_color {
  color: #080707;
}

.slds-pager_color:hover {
  color: #080707;
}

.slds-theme_success {
  background-color: #4bca81;
}

.slds-admin-view {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 50vh;
}


/* Animations */

@keyframes notificationup {
  0%   {
    transform: translate(0, 30px);
  }
  70% {
    transform: translate(0, -10px);
  }
  100% {
    transform: translate(0, 0);
  }
}